body {
    background-color: var(--lightGreen);
}

.navBarStyle {
    display: flex;
    flex-direction: row;
    width: 500px;

    margin: 0px auto;

    margin-top: 100px;


}

@media screen and (max-width : 500px) {
    .navBarStyle {
        width: 300px;
    }
}

.navBarText {

    margin: 0px auto;
    display: flex;
    align-items: center;
    color: white;
    font-weight: bolder;
    font-size: 1.5rem;
    transition: 1s;
    border-radius: 20px;
    text-decoration: none;





    &:hover {
        transition: 0.5s;
        background-color: rgb(107, 172, 147);


    }

}